import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { Photo } from '../interfaces/Photo'

@Injectable({
  providedIn: 'root'
})
export class PhotoService {

  URI = 'http://localhost:4000/api/photos'

  constructor(private http: HttpClient) { }
  createPhoto(mo: string, brand:string, price: string, operator: string, so: string, color: string, photo: File){

    const fd = new FormData();
    fd.append('mo', mo );
    fd.append('brand', brand );
    fd.append('price', price );
    fd.append('operator', operator );
    fd.append('so', so );
    fd.append('color', color );
    fd.append('image', photo );
    return this.http.post(this.URI, fd);
  }

  getPhotos(){
    return this.http.get<Photo[]>(this.URI);
  }
  getPhoto(id: string) {
    return this.http.get<Photo>(`${this.URI}/${id}`);
  }

  deletePhoto(id: string) {
    return this.http.delete(`${this.URI}/${id}`);
  }
  updatePhoto(id: string, mo: string, brand: string, price: string,
    operator: string, so: string, color: string ) {
   return this.http.put(`${this.URI}/${id}`, {mo, brand, price, operator, so, color});
 }
}

