import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'
import { PhotoService } from '../../services/photo.service'
import { Photo } from "../../interfaces/Photo"

@Component({
  selector: 'app-photo-preview',
  templateUrl: './photo-preview.component.html',
  styleUrls: ['./photo-preview.component.css']
})
export class PhotoPreviewComponent implements OnInit {

id: string;
photo: Photo;

  constructor(private activatedRoute: ActivatedRoute, private router: Router,
     private photoService: PhotoService) { }

  ngOnInit(){
    this.activatedRoute.params.subscribe(params =>{
      this.id = params['id'];
      this.photoService.getPhoto(this.id)
      .subscribe(
        res => {
          this.photo = res;
        },
        err => console.log(err)
      )
    })
  }

  deletePhoto(id: string) {
    this.photoService.deletePhoto(id)
      .subscribe(res => {
        console.log(res)
        this.router.navigate(['/photos']);
      })
  }
  updatePhoto(mo: HTMLInputElement, brand: HTMLInputElement, price: HTMLInputElement,
    operator: HTMLInputElement, so: HTMLInputElement, color: HTMLInputElement): boolean {
      this.photoService.updatePhoto(this.photo._id, mo.value, brand.value, price.value, operator.value, so.value, color.value)
      .subscribe(res => {
        console.log(res);
        this.router.navigate(['/photos']);
      });
    return false;
   
  }

}
